.add-icon {
  width: 32px;
  cursor: pointer;
  margin-right: 20px;
  transition: all .3s;
}

.add-icon:hover {
  transform: scale(1.2);
}

.close {
  position: absolute;
  top: -5px;
  right: -5px;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  cursor: pointer;
  transition: all .3s
}

.close:hover {
  transform: scale(1.2);
}

.orders {
  width: 100%;
}

.orders-title {
  display: flex;
  align-items: center;
  width: 80%;
  margin: 0 auto;
}

.orders-content {
  display: flex;
  width: 80%;
  margin: 0 auto;
}

.order_info {
  position: relative;
  width: 500px;
  margin-left: 10px;
  padding: 24px;
  background-color: white;
  border: 1px sloid grey;
  border-radius: 10px;
}

.order_info__add {
  display: flex;
  align-items: center;
}

.product {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  margin: 0 auto;
  background-color: white;
  border: 1px solid rgb(128, 128, 128);
  transition: all .3s;
  width: 80%;
}

.product:hover {
  box-shadow: 5px 5px 10px rgba(128, 128, 128, 0.8);
}

.product_img {
  width: 32px;
  height: 32px;
  background-image: url('data:image/jpeg;base64,');
  background-size: cover;
  background-repeat: no-repeat;
}

.product_title {
  width: 40%;
}

.product_status {
  width: 20%;
}

.remove_icon {
  width: 16px;
  cursor: pointer;
  transition: all .3s;
}

.remove_icon:hover {
  transform: scale(1.2);
}
