.product-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 24px;
  background-color: white;
  border: 1px solid rgb(128, 128, 128);
  border-radius: 10px;
  transition: all .3s;
}

.product-item:hover {
  box-shadow: 5px 5px 10px rgba(128, 128, 128, 0.8);
}

.product-img {
  width: 32px;
  height: 32px;
  background-image: url('data:image/jpeg;base64,');
  background-size: cover;
  background-repeat: no-repeat;
}

.product-title {
  width: 20%;
}

.product-status {
  width: 10%;
}

.product-date {
  width: 10%;
}

.product-age {
  width: 10%;
}

.product-price {
  width: 10%;
}

.product-order-title {
  width: 20%;
}

.remove-icon {
  width: 16px;
  cursor: pointer;
}
